import _extractNumberWithoutDigits from './_extractNumberWithoutDigits'
import _extractDigits from './_extractDigits'
import _calculate from './_calculate'

export default function _isValid (object, number) {
  let numberWithoutDigit = _extractNumberWithoutDigits(object, number)
  const digits = _extractDigits(object, number)

  for (const digit of digits) {
    const calculatedDigit = _calculate(object, numberWithoutDigit)

    if (calculatedDigit !== parseInt(digit)) return false

    numberWithoutDigit += digit
  }
  return true
}
