import _isCheckDigitValid from './_isCheckDigitValid'

export default function ieBA (element) {
  const errorMessage = I18n.t('errors.messages.invalid')
  const number = element.value.replace(/[^\d]+/g, '')
  const validLengths = [8, 9]

  if (!validLengths.includes(number.length)) return errorMessage

  return _isCheckDigitValid(number) || errorMessage
}
