import _multiplyWeights from './_multiplyWeights'

export default function _calculate (object, number) {
  const multipliedWeights = _multiplyWeights(object, number)
  const calculatedModule = multipliedWeights % object._module
  const subtractionByModule = Math.abs(calculatedModule - object._module)

  if (object._withoutModuleComplement) return calculatedModule

  return subtractionByModule <= 9 ? subtractionByModule : 0
}
