import _ieBase from '../_ieBase'

import _isCheckDigitValid from './_isCheckDigitValid'
import _isInitialCharsValid from './_isInitialCharsValid'

export default function ieGO (element) {
  const number = element.value.replace(/[^\d]+/g, '')
  const initialChars = ['10', '11', '15']

  return _ieBase(element.value, {
    numDigits: 1,
    customConditions: _isCheckDigitValid(number) && _isInitialCharsValid(number, initialChars),
    ignoresCheckDigit: true,
  })
}
