import _isValid from './_isValid'
import _multiplyWeights from './_multiplyWeights'
import _extractNumberWithoutDigits from './_extractNumberWithoutDigits'
import _extractDigits from './_extractDigits'
import _calculate from './_calculate'

export default class CheckDigit {
  constructor (userOptions) {
    const options = Object.assign({
      numDigits: 2,
      module: 11,
      weights: [2, 3, 4, 5, 6, 7, 8, 9],
      withoutModuleComplement: false
    }, userOptions)

    this._numDigits = options.numDigits
    this._module = options.module
    this._weights = options.weights
    this._withoutModuleComplement = options.withoutModuleComplement
  }

  multiplyWeights (number) {
    return _multiplyWeights(this, number)
  }

  extractNumberWithoutDigits (number) {
    return _extractNumberWithoutDigits(this, number)
  }

  extractDigits (number) {
    return _extractDigits(this, number)
  }

  calculate (number) {
    return _calculate(this, number)
  }

  isValid (number) {
    return _isValid(this, number)
  }

  get module () {
    return this._module
  }
}
