import _validateFirstDigit  from './_validateFirstDigit'
import _validateSecondDigit from './_validateSecondDigit'

export default function ieMG (element) {
  const number = element.value.replace(/[^\d]+/g, '')
  const errorMessage = I18n.t('errors.messages.invalid')

  if (number.length !== 13) return errorMessage

  return _validateFirstDigit(number) && _validateSecondDigit(number) || errorMessage
}
