import _ieBase from './_ieBase'

export default function _ieAL (element) {
  const companyType = parseInt(element.value.replace(/[^\d]+/g, '')[2])
  const companyAcceptedTypes = [0, 3, 5, 7, 8]

  return _ieBase(element.value, {
    numDigits: 1,
    initialChars: '24',
    customConditions: companyAcceptedTypes.includes(companyType)
  })
}
