// import CheckDigit from '../../check-digit'

import _getGroup from './_getGroup'
import _defineCheckDigits from './_defineCheckDigits'
import _calculate from './_calculate'

export default function _isCheckDigitValid (number) {
  const group = _getGroup(number, number.length)
  const checkDigits = _defineCheckDigits(group, number.length)
  const numberWithoutDigit = checkDigits[0].extractNumberWithoutDigits(number)
  const digits = checkDigits[0].extractDigits(number)
  const calculatedDigits = _calculate(...checkDigits, numberWithoutDigit)

  return (
    parseInt(digits.charAt(0)) === calculatedDigits[0] &&
    parseInt(digits.charAt(1)) === calculatedDigits[1]
  )
}
