import CheckDigit from '../check-digit'

export default function _suframa (element) {
  const errorMessage = I18n.t('errors.messages.invalid')
  const lengths = [8, 9]
  const numDigits = 1
  const weights = [2, 3, 4, 5, 6, 7, 8, 9]
  const checkDigit = new CheckDigit({ weights: weights, numDigits: numDigits })
  const value = element.value.replace(/[^\d]+/g, '')

  if (!lengths.includes(value.length)) return errorMessage
  if (value.substring(0, 2) === '00') return errorMessage
  if (!checkDigit.isValid(value)) return errorMessage

  return true
}
