import _ieSPForIndustryAndCommerce from './_ieSPForIndustryAndCommerce'
import _ieSPForRural from './_ieSPForRural'

export default function ieSP (element) {
  const ruralPrefix = ['P', 'p']
  const number = element.value.replace(/[\W|_]+/g, '')

  if (ruralPrefix.includes(number.charAt(0))) {
    return _ieSPForRural(number)
  }

  return _ieSPForIndustryAndCommerce(number)
}
