import _toDigit from './_toDigit'

import CheckDigit from '../../check-digit'

export default function _ieSPForRural (value) {
  const errorMessage = I18n.t('errors.messages.invalid')

  if (value.length !== 13) return errorMessage

  const number = value.substring(1, 9)
  const numberCheckDigit = value.charAt(9)

  const checkDigit = new CheckDigit({
    numDigits: 1,
    weights: [10, 8, 7, 6, 5, 4, 3, 1],
    withoutModuleComplement: true
  })

  if (_toDigit(checkDigit.calculate(number)) !== numberCheckDigit) {
    return errorMessage
  }

  return true
}
