import CheckDigit from '../../check-digit'
import _calculateDigit from './_calculateDigit'
import _getAuxValues from './_getAuxValues'

export default function _isCheckDigitValid (number) {
  const checkDigit = new CheckDigit ({ numDigits: 1 })
  const numberWithoutDigits = checkDigit.extractNumberWithoutDigits(number)
  const auxValues = _getAuxValues(numberWithoutDigits)
  const calculatedDigit = _calculateDigit(checkDigit, numberWithoutDigits, auxValues)
  const digit = checkDigit.extractDigits(number)

  return parseInt(digit) === calculatedDigit
}
