import _ieBase from './_ieBase'

export default function _ieTO (element) {
  const unMaskedValue = element.value.replace(/[^\d]+/g, '')
  const companyType = unMaskedValue.substring(2, 4)
  const companyAcceptedTypes = ['01', '02', '03']

  return _ieBase(unMaskedValue.replace(companyType, ''), {
    numDigits: 1,
    customConditions: companyAcceptedTypes.includes(companyType)
  })
}
