import CheckDigit            from '../../check-digit'
import _addZeroToNumber      from './_addZeroToNumber'
import _sumMultipliedNumbers from './_sumMultipliedNumbers'
import _subtractionNumber    from './_subtractionNumber'

export default function _validateFirstDigit (number) {
  const checkDigit          = new CheckDigit()
  const digits              = checkDigit.extractDigits(number)
  const numberWithoutDigits = checkDigit.extractNumberWithoutDigits(number)
  const numberWithZero      = _addZeroToNumber(numberWithoutDigits)
  const sum                 = _sumMultipliedNumbers(numberWithZero)
  const firstDigit          = _subtractionNumber(sum)

  return firstDigit === parseInt(digits[0])
}
